<template>
    <div id="activate-account">
        <div class="auth-box-w wider centered" v-if="user" >
            <div class="logo-w">
                <router-link :to="{name:'home'}">
                    <img alt="" :src="$store.state.CONSTANTS.configs.LOGO_PATH" class="img-fluid">
                </router-link>
            </div>
            <h5 class="auth-header">
                Seja bem vindo, {{user.name}} !
            </h5>
            <h5 class="text-success">
                <small>Sua conta foi ativada com sucesso!</small>
            </h5>
            <div class="logged-user-w m-t m-b">
                <div class="avatar-w">
                    <img alt="" :src="user.photo_url">
                </div>
                <div class="logged-user-name">
                    {{user.name}}
                </div>

                <router-link :to="{name:'login'}" class="btn btn-primary btn-lg ">Entrar</router-link>
            </div>

        </div>

    </div>
</template>

<script>
    import handle_errors from '../../mixins/handle_errors'

    export default {
        mixins: [handle_errors],
        data() {
            return {
                user: '',
            }
        },
        methods: {
            activateUser() {
                let code = window.location.href.split('?')[1];

                this.$store.dispatch('activateUser', code)
                    .then(response => {
                            this.user = response.data.user;
                    })
                    .catch(err => {
                        this.handleErrors(err);
                        this.$router.push({name: 'login'})
                    })
                    .finally(() => {
                        this.preload(false);
                    });
            },
        },
        created() {
            this.preload(true);
            this.activateUser();

        },
    }
</script>
<style scoped lang="scss">

    .btn.btn-primary {
        margin: 36px;
    }
    .avatar-w{
        margin-top: 25px;

    }

    .logo-w{
        $heigth : 250px;
        max-height: $heigth;
        min-height: $heigth;
        /*padding-top: $heigth/5;
        padding-bottom: $heigth/5;*/
        img{
            max-height: $heigth - $heigth/10 ;

        }

    }

</style>